<template>
  <div class="search">
    <div class="searchbar">
      <div class="tabbar">
        <div :class="tab==='all'?'selbar':''" @click="seltab('all')">全部</div>
        <div :class="tab==='ans'?'selbar':''" @click="seltab('ans')">答案</div>
        <div :class="tab==='user'?'selbar':''" @click="seltab('user')">用户</div>
      </div>
      <div class="search-box" v-if="tab==='all'">
        <div class="search-all">
          <collect :collect="allData"></collect>
          <div class="empty">没有更多数据了</div>
        </div>
      </div>
      <div class="search-box" v-if="tab==='ans'">
        <collect :collect="question"></collect>
        <div class="empty">没有更多数据了</div>
      </div>
      <div class="user-box" v-if="tab==='user'">
        <users :users="users"></users>
        <div class="empty">没有更多数据了</div>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "search",
    data() {
      return {
        value: '',
        allData: [],
        tab: 'all',
        question: [],
        users: [],
      }
    },
    mounted() {
      this.init();
      this.serchAll()
    },
    active() {
      this.init()
    },
    watch: {
      $route() {
        this.value = this.$route.query.search;
      },
    },
    methods: {
      init() {
        this.value = this.$route.query.search;
      },
      seltab(tab) {
        this.tab = tab;
        switch (tab) {
          case "ans":
            this.serchQuestion();
            break;
          case "user":
            this.serchUser();
            break;
          default:
            this.serchAll()
        }
      },
      serchAll() {
        let like = this.value;
        this.$post('index/data/question_answer', {like}).then(res => {
          this.allData = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //获取搜索用户
      serchUser() {
        let like = this.value;
        this.$post('index/data/sos_user', {like}).then(res => {
          this.users = res.data.data;
        }).catch(err => {
          console.log(err);
        })
      },

      //获取搜索提问
      serchQuestion() {
        let like = this.value;
        this.$post('index/data/sos_question', {like}).then(res => {
          this.question = res.data;
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .searchbar {
    margin: 26px auto 0;
    width: 680px;
    min-height: 400px;
    background: rgba(254, 254, 254, 1);
    box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.12);
    border-radius: 10px;

    .empty {
      width: 100%;
      text-align: center;
      padding: 40px 0;
      color: #8c939d;
    }

    .tabbar {
      width: 100%;
      height: 43px;
      line-height: 43px;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      padding: 0 25px;
      box-sizing: border-box;
      display: flex;
      justify-content: flex-start;

      > div {
        height: 100%;
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        margin-right: 52px;
        cursor: pointer;
      }

      .selbar {
        position: relative;
        font-size: 16px;
        font-weight: bold;
        color: rgba(51, 51, 51, 1);

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 4px;
          background: rgba(0, 150, 255, 1);
          border-radius: 2px;
        }
      }
    }

    .search-box {

    }
  }
</style>



